@mixin noteText() {
    &--text{
        padding: 10px;
        font-size: 14px;
        width: 100%;
        &:empty{
            &::before{
                color: rgba(0, 0, 0, 0.6);
                content: 'Add your notes...';
            }
        }
        &:focus{
            outline: none;
        }
    }
}