@import './navbar/index.scss';
@import './partials/note.scss';
@import './partials/notes.scss';
@import './partials/note-header.scss';
@import './partials/note-text.scss';
@import './partials/note-bubble.scss';
@import './partials/note-menu.scss';
.rs-notes{
    *,*::before,*::after{
        box-sizing: border-box;
    }
    z-index: 4;
    @include notes();
    @include note();
    @include navbar();
    @include noteHeader();
    @include noteText();
    @include noteMenu();
    @include noteBubble();
}
