* {
  padding: 0;
  margin: 0;
}

html,
body {
  min-height: 100% !important;
  height: 100%;
  background-color: rgb(53, 54, 58);
  color: white;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif !important;
}

/* Style for vertical scrollbar */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* Handle style for both vertical and horizontal scrollbars */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle hover style for both vertical and horizontal scrollbars */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}