@mixin noteBubble() {
    &--note{
        &__bubble{
            cursor: move;
            border: none;
            outline: none;
            position: relative;
            &::before{
                display: block;
                opacity: 0;
                content: attr(title);
                transform: translate(-50%, -150%);
                transform-origin: 0 0;
                transition: all linear .4s;
                overflow: hidden;
                background-color: var(--background-color);
                white-space: nowrap;
                position: absolute;
                left: 50%;
                top: 50%;
                padding: 5px 8px;
                border-radius: 5px;
                color: rgba(0, 0, 0, 0.87);
                font-size: .9em;
                pointer-events:none;
            }
            &:hover{
                cursor: pointer;
                &::before{
                    opacity: 1;
                }
            }
            &:active{
                opacity: .5;
                z-index: 3;
            }
        }
    }
    
}
